body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.App {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* background-color: black; */
}
.restriction {
  /* width: 50%; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* border: 1px solid black;
  border-radius: 4px; */
  /* margin: 10px;*/
  padding: 5px;
}
.restriction-select {
  min-width: 25vw;
  width: 100%;
}
.restriction-label {
  width: 100%;
  margin-block: 5px;
}

.footer {
  display: flex;
  position: absolute;
  bottom: 0;
  width: 95%;
  /* border-top: #fa9401 solid 3px; */
  justify-content: space-between;
  align-items: baseline;
  z-index: 1;
}
.contact-us-p {
  align-self: baseline;
}
.navbar {
  display: flex;
  position: absolute;
  top: 0;
  width: 100%;
  border-bottom: #fa9401 solid 3px;
  z-index: 3;
  max-height: 20px;
}
.nav-link {
  padding-inline: 5px;
  text-decoration: none;
}
.nav-link:visited {
  color: inherit;
}
.current-nav {
  color: #fa9401 !important;
  align-self: center;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}
.existing-restrictions {
  padding-block: 30px;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  /* background-color: #fa9401; */
  width: auto;
  /* padding-top: 10%; */
  z-index: 2;
  justify-content: space-evenly;
}
.button-div {
  margin: 5px;
  display: flex;
  width: 100%;
  justify-content: center;
}
.create-restriction {
  align-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.loader-div {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader {
  width: 150px;
  height: 150px;
  color: #0085ff;
}
.small-loader-div {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  color: #0085ff;
}
.small-loader {
  width: 20px !important;
  height: 20px !important;
}
